@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fade-out {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes fade-out {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@-webkit-keyframes slide-down {
  from {
    padding-top: 0px;
    opacity: 0; }
  to {
    padding-top: 20px;
    opacity: 1; } }

@keyframes slide-down {
  from {
    padding-top: 0px;
    opacity: 0; }
  to {
    padding-top: 20px;
    opacity: 1; } }

#gallery-container {
  font-size: 21px;
  max-width: 1150px;
  margin: auto; }
  @media only screen and (max-width: 650px) {
    #gallery-container {
      font-size: 16px; } }
  #gallery-container #gallery-title {
    font-size: 48px;
    padding: 0px 40px;
    margin-top: 20px;
    float: left; }
    @media only screen and (max-width: 650px) {
      #gallery-container #gallery-title {
        font-size: 32px;
        text-align: center;
        float: none; } }
  #gallery-container #gallery-content {
    width: auto;
    overflow: hidden; }



