.signature-clip .clip-stroke {
  stroke: #555555;
  stroke-width: 1; }

.signature-stroke {
  stroke: #555555;
  stroke-linecap: round;
  fill: none; }
  .signature-stroke .stroke-J,
  .signature-stroke .stroke-dot,
  .signature-stroke .stroke-W,
  .signature-stroke .stroke-ong {
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    stroke-width: 0;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s; }
  .signature-stroke .stroke-J {
    -webkit-animation: show-J 3s forwards;
            animation: show-J 3s forwards; }
  .signature-stroke .stroke-dot {
    -webkit-animation: show-dot 3s forwards;
            animation: show-dot 3s forwards; }
  .signature-stroke .stroke-W {
    -webkit-animation: show-W 3s forwards;
            animation: show-W 3s forwards; }
  .signature-stroke .stroke-ong {
