            animation: show-ong 3s forwards; }

@-webkit-keyframes show-J {
  0% {
    stroke-dashoffset: 1000;
    stroke-width: 0; }
  20%,
  100% {
    stroke-dashoffset: 200;
    stroke-width: 10; } }

@keyframes show-J {
  0% {
    stroke-dashoffset: 1000;
    stroke-width: 0; }
  20%,
