    stroke-dashoffset: 1000;
    stroke-width: 0; }
  30%,
  100% {
    stroke-dashoffset: 950;
    stroke-width: 10; } }

@-webkit-keyframes show-W {
  0%,
  30% {
    stroke-dashoffset: 1000;
    stroke-width: 0; }
  60%,
  100% {
    stroke-dashoffset: 0;
    stroke-width: 10; } }

@keyframes show-W {
  0%,
  30% {
    stroke-dashoffset: 1000;
    stroke-width: 0; }
  60%,
  100% {
    stroke-dashoffset: 0;
    stroke-width: 10; } }

@-webkit-keyframes show-ong {
  0%,
  50% {
    stroke-dashoffset: 1005;
    stroke-width: 4; }
  100% {
    stroke-dashoffset: 0;
    stroke-width: 10; } }

@keyframes show-ong {
  0%,
  50% {
    stroke-dashoffset: 1005;
