@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fade-out {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes fade-out {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@-webkit-keyframes slide-down {
  from {
    padding-top: 0px;
    opacity: 0; }
  to {
    padding-top: 20px;
    opacity: 1; } }

@keyframes slide-down {
  from {
    padding-top: 0px;
    opacity: 0; }
  to {
    padding-top: 20px;
    opacity: 1; } }

.logo-group .logo-path,
.logo-group .logo-dot {
  stroke: #555555;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  stroke-width: 0;
  fill: none;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }

.logo-group .logo-path {
  -webkit-animation: show-logo-path 1.5s forwards;
          animation: show-logo-path 1.5s forwards; }

.logo-group .logo-dot {
  fill: #555555;
  opacity: 0;
  -webkit-animation: show-logo-dot 1.5s forwards;
          animation: show-logo-dot 1.5s forwards; }

@-webkit-keyframes show-logo-path {
  0% {
    stroke-dashoffset: 1000;
    stroke-width: 0; }
  100% {
    stroke-dashoffset: 650;
    stroke-width: 5; } }

@keyframes show-logo-path {
  0% {
    stroke-dashoffset: 1000;
    stroke-width: 0; }
  100% {
    stroke-dashoffset: 650;
    stroke-width: 5; } }

@-webkit-keyframes show-logo-dot {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes show-logo-dot {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }



